.restaurant-tile {
  position: relative;
  width: 254px;
  margin-top: 30px;
  margin-bottom: 60px;
}

.restaurant-tile-wrapper {
  background: #fff;
  display: block;
  text-decoration: none;
  color: inherit;
  margin: -20px -20px -41px;
}

.restaurant-tile-body {
  position: relative;
  padding: 20px 20px 57px;
  border: 1px solid #fff;
  contain: content;

  width: 212px;
}

.restaurant-tile-body-image-wrapper {
  background: #ffffff;
  width: 254px;
  height: 160px;
  position: relative;
}

.restaurant-tile-body-title-wrapper {
  margin-top: 14px;
}

.restaurant-tile-body-title {
  font-size: 17px;
  font-weight: 500;
  word-break: break-word;
}

.restaurant-tile-body-subtitle {
  color: #686b78;
  font-size: 13px;
  margin-top: 4px;
}

.restaurant-tile-body-desc-wrapper {
  display: flex;
  align-items: center;

  margin-top: 18px;
  font-size: 12px;
  -ms-flex-pack: justify;
  justify-content: space-between;
  color: #535665;
}

.restaurant-tile-body-desc-ratings {
  display: flex;
  align-items: center;

  color: #000000;

  height: 20px;
  width: 43px;
  padding: 0 5px;
  font-weight: 400;
}

.star {
  font-size: 15px;
  font-weight: bold;
  margin-right: 4px;
  position: relative;
  color: aquamarine;
  top: -1px;
}

[class^="icon-"] {
  font-family: icomoon;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.restaurant-tile-body-desc-ratings .icon-star {
  font-size: 10px;
  margin-right: 4px;
  position: relative;
  top: -1px;
}

.restaurant-tile-body-quick-view {
  color: #686b78;
  font-size: 13px;
  visibility: hidden;
  border-top: 1px solid #e9e9eb;
  padding-top: 14px;
  margin-top: 14px;
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: 14px;
}

.restaurant-tile-body-quick-view-title {
  text-align: center;
  float: none;
  display: block;
  margin: 0 auto;
  width: 100px;
  color: #5d8ed5;
  /* float: right; */
  text-transform: uppercase;
  font-weight: 600;
}

.restaurant-tile-body:hover {
  border-color: #d3d5df;
  box-shadow: 0 4px 7px 0 rgba(218, 220, 230, 0.6);
}

.restaurant-tile-body:hover .restaurant-tile-body-quick-view {
  visibility: visible;
}
